import { InnerBlocks, RichText, useBlockProps } from "@wordpress/block-editor";
// import { __ } from '@wordpress/i18n';

import "./editor.scss";

const Edit = (props) => {
	const { attributes, setAttributes, clientId } = props;
	const { title, itemId } = attributes;

	setAttributes({
		itemId: "itemid-" + clientId.slice(0, 8),
	});

	function updateTitle(newTitle) {
		setAttributes({ title: newTitle });
	}

	function iconChanged(e) {
		e.stopPropagation();
		const accordionButton = e.target.closest(".accordion-button");
		if (accordionButton.classList.contains("collapsed")) {
			accordionButton
				.querySelector(".na-collapse-icon")
				.classList.remove("show");
			accordionButton.querySelector(".na-expand-icon").classList.add("show");
		} else {
			accordionButton.querySelector(".na-expand-icon").classList.remove("show");
			accordionButton.querySelector(".na-collapse-icon").classList.add("show");
		}
	}

	return (
		<>
			<div
				{...useBlockProps({
					className: `accordion-item`,
				})}
			>
				<h3
					className="accordion-header"
					id={"heading-" + itemId}
					style={{ margin: 0 }}
				>
					<button
						onClick={(e) => iconChanged(e)}
						className="accordion-button"
						type="button"
						data-bs-toggle="collapse"
						data-bs-target={"#collapse-" + itemId}
						aria-expanded="true"
						aria-controls={"collapse-" + itemId}
					>
						<i className="na-expand-icon fa fa-plus"></i>
						<i className="na-collapse-icon show fa fa-minus"></i>
						<RichText
							key="editable"
							tagName="p"
							onChange={updateTitle}
							value={title}
						/>
					</button>
				</h3>
				<div
					id={"collapse-" + itemId}
					className="accordion-collapse collapse show"
					aria-labelledby={"heading-" + itemId}
				>
					<div className="accordion-body">
						<InnerBlocks
							allowedBlocks={true}
							template={[
								[
									"core/paragraph",
									{
										placeholder: "Write accordion content here or add block.",
									},
								],
							]}
						/>
					</div>
				</div>
			</div>
		</>
	);
};
export default Edit;
